span#spinner{
  margin: 45px auto;
  text-align: center;
  display: block;
  color: #dfdfdf;
  font-size: 300%;
  -webkit-animation: 1s infinite spin ease-in-out;
          animation: 1s infinite spin ease-in-out;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s
}

@-webkit-keyframes spin{
  from{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin{
  from{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



